<template>
  
  <img v-if="img" :src="img" alt="bg">
  <div class="bg-dark"></div>
  <div class="indecision-container">

      <h1>El Adivinador</h1>

      <input 
        type="text" 
        placeholder="Hazme una pregunta"
        v-model="question"
        >
      <small>*Terminar con un signo de interrogacion (?)</small>
  
    <div v-if="isValidQuestion" class="text-box">
        <h2>{{question}}</h2>
        
            <p v-show="consultando" class="consultando">{{consultando}}</p>

            <div v-if="respuestaPositiva">
                <p class="respuesta"
                    v-bind:class="{positiva: respuestaPositiva}"
                >{{respuesta}}</p>
            </div>

            <div v-if="!respuestaPositiva">
                <p class="respuesta"
                    v-bind:class="{negativa: !respuestaPositiva}"
                >{{respuesta}}</p>
            </div>

    </div>
  
  </div>

</template>

<script>
export default {
    data() {
        return {
            question: null,
            consultando: null,
            respuesta: null,
            img: null,
            isValidQuestion: false,
            respuestaPositiva: null
        }  
    },

    methods: {
        async getAnswer() {
            this.consultando = 'Consultando con los astros...'
            const {answer, image } = await fetch('https://yesno.wtf/api').then( resp => resp.json() )
            this.consultando = null
            this.respuesta = answer === 'yes' ? 'Si!' : 'No!'
            this.img = image
            this.respuestaPositiva = answer === 'yes' ? true : false

            }
    },

    watch: {
        question( value, oldValue) {

            this.isValidQuestion = false;

            if ( !value.includes('?') ) return

            this.isValidQuestion = true;
            this.getAnswer()
            
        }
    }
}
</script>

<style scoped>

img, .bg-dark {
        height: 100vh;
        left: 0px;
        max-height: 100%;
        max-width: 100%;
        position: fixed;
        top: 0px;
        width: 100vw;
    }

    .bg-dark {
        background-color: rgba(0, 0, 0, 0.4);
    }

    .indecision-container {
        position: relative;
        z-index: 99;
    }
    
    input {
        width: 250px;
        padding: 10px 15px;
        border-radius: 5px;
        border: none;
        margin-bottom: 10px;
    }
    input:focus {
        outline: none;
    }

    small {
        display: block;
        color: rgb(177, 177, 177);
        font-size: 15px;
        margin-top: 5px;
    }

    .respuesta {
        color: rgb(150, 150, 150);
        text-transform: uppercase;
        font-size: 25px;
    }
    
    .respuesta.negativa {
        color: red;
        text-transform: uppercase;
        font-size: 25px;
    }
    .respuesta.positiva {
        color: green;
        text-transform: uppercase;
        font-size: 25px;
    }
    
    h2 {
        color: white;
    }

    .text-box {
        width: 100%;
        max-width: 350px;
        height: 150px;
        background-color: rgba(0, 0, 0, 0.466);
        padding: 15px 15px;
        text-align: center;
        margin: 35px auto;
        /* position: relative; */
        display: flex; 
        flex-direction: column;
        justify-content: center;
        align-content: center;
        border-radius: 15px;
    }

    .consultando {
        color: rgb(255, 136, 0);
        font-size: 25px;
    }

</style>